import {
    getEmployList,
    deleteEmployInfo
} from "../../../../../../utils/employment.url";
// import employDialog from "./../commonModel/employDialog.vue";
export default {
    components: {
        // employDialog
    },
    data() {
        return {
            input: '',
            employList: [], //用户列表
            search: '',
            pageObj: {
                pageRowNum: 8,
                currPage: 1,
                totalRow: 20,
            },
            dialogFormVisible: false,
            editEmployInfo: {},
        }
    },
    methods: {
        getEmployList(pageObj) {
            getEmployList(pageObj, "get").then(res => {
                if (res.status == 200) {
                    this.employList = res.data.employList;
                    this.pageObj = res.data.pageObj;
                    this.employList.map((item,index) =>{
                        item.contractTime = item.contractTime.split(
                            "T"
                          )[0];
                    })
                }
            })
        },
        changePage(val) {
            this.pageObj.currPage = val;
            this.getEmployList(this.pageObj);
        },
        dialogClose(value) {
            this.$store.commit('changeBasicStatus', false);
            this.dialogFormVisible = value;
            this.getEmployList(this.pageObj);
        },
        handleEdit(index, row) {
            // console.log(row)
            this.editEmployInfo = row;
            this.dialogFormVisible = true;
            this.$store.commit('changeBasicStatus', true);
        },
        handleDelete(index, row) {
            this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                deleteEmployInfo(row, "delete").then(res => {
                    if (res.status == 200) {
                        this.getEmployList(this.pageObj);
                        this.$message({
                            message: res.message,
                            type: "success"
                        });
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
    },
    created() {
        this.getEmployList(this.pageObj);
    }
}